import React, { useState } from 'react';
import Quest from '../../accountmenu/uikit/Quest';
import BigButton from '../../accountmenu/uikit/BigButton';
import '../css/jobsgov.css';
import MdPerson from 'react-ionicons/lib/MdPerson';

const JobsGov = ({ jobIndex, jobname }) => {
  const jobs = [
    {
      title: ' 1',
      img: '',
      desc: `
                <p class="accountmenu__content__cards__answer__text">
                        ,      ,   3   .     ,     ,    VIP.
                </p>
            `,
      subtitle: '   ',
      minidesc: '      3- ',
    },
    {
      title: ' 2',
      img: '',
      desc: `
                <p class="accountmenu__content__cards__answer__text">
                        ,      ,   3   .     ,     ,    VIP.
                </p>
            `,
      subtitle: '   ',
      minidesc: '      3- ',
    },
    {
      title: ' 3',
      img: '',
      desc: `
                <p class="accountmenu__content__cards__answer__text">
                        ,      ,   3   .     ,     ,    VIP.
                </p>
            `,
      subtitle: '   ',
      minidesc: '      3- ',
    },
    {
      title: ' 4',
      img: '',
      desc: `
                <p class="accountmenu__content__cards__answer__text">
                        ,      ,   3   .     ,     ,    VIP.
                </p>
            `,
      subtitle: '   ',
      minidesc: '      3- ',
    },
    {
      title: ' 5',
      img: '',
      desc: `
                <p class="accountmenu__content__cards__answer__text">
                        ,      ,   3   .     ,     ,    VIP.
                </p>
            `,
      subtitle: '   ',
      minidesc: '      3- ',
    },
  ];

  const [active, setActive] = useState(0);

  return (
    <React.Fragment>
      <div className='accountmenu__content__cards__item accountmenu__scrollable'>
        <div className='accountmenu__content__cards__container'>
          <span className='accountmenu__content__cards__header__name'></span>
          <div className='accountmenu__hr' style={{ marginTop: '4%', marginBottom: '4%' }} />
          {jobIndex !== -1 && jobname && (
            <React.Fragment>
              <div className='govmenu__jobbar'>
                <span className='govmenu__jobbar__text'> :</span>
                <span className='govmenu__jobbar__name'>{jobname}</span>
              </div>
              <BigButton text={''} onPress={() => console.log(' ')} disabled={false} type={1} />
            </React.Fragment>
          )}
          <div className='accountmenu__cards__question__container'>
            {jobs.map((item, index) => (
              <Quest
                title={item.title}
                subtitle={item.subtitle}
                key={index.toString()}
                index={index}
                active={active}
                setActive={() => setActive(index)}
                done={item.done}
                children={<MdPerson fontSize='2.962962962962963vh' color='white' />}
              />
            ))}
          </div>
        </div>
      </div>
      <div className='accountmenu__content__cards__blitem'>
        <div className='accountmenu__content__cards__questinfo__container'>
          <img src={'https://os-rp.com/state99/client/images/mmenu/all/quest-bg.png'} className='accountmenu__content__cards__questinfo__img__container' alt='' />
          <div className='accountmenu__content__cards__questinfo__header'>
            <span className='accountmenu__content__cards__questinfo__header__name'>{jobs[active].title}</span>
            <span className='accountmenu__content__cards__questinfo__header__count'>{jobs[active].minidesc}</span>
            <div className='govmenu__content__cards__answer__text__container' dangerouslySetInnerHTML={{ __html: jobs[active].desc }} />
            <BigButton
              text={jobIndex === -1 ? '' : jobIndex === active ? '' : ''}
              onPress={() => console.log(' ')}
              disabled={false}
              type={jobIndex === -1 ? 0 : jobIndex === active ? 1 : 0}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default JobsGov;
